body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.hamburger {
    display: flex;
    flex-wrap: wrap;
    width: 30px;
    margin: 5px;
    padding: 5px;
    cursor: pointer;
    box-sizing: border-box;
    border-radius: 2px;
}

.hamburger:hover .hamburgerDot {
    background: black;
}

.hamburgerDot {
    height: 4px;
    width: 4px;
    margin: 1px;
    background: gray;
}

.arrowTop {
    border-color: transparent;
    border-style: dashed dashed solid;
    border-width: 0 8.5px 8.5px;
    /* display: none; */
    position: absolute;

    /* if hamburger shifts around, it can be shifted back here */
    left: 10px;
    z-index: 5;
    height: 0;
    width: 0;
    border-bottom-color: #ccc;
    border-bottom-color: rgba(0, 0, 0, 0.2);
    top: 39px;
}

.arrowBottom {
    border-color: transparent;
    border-bottom-color: #fff;
    border-style: dashed dashed solid;
    border-width: 0 8.5px 8.5px;
    /* display: none; */
    position: absolute;
    left: 10px;
    top: 40px;
    z-index: 6;
    height: 0;
    width: 0;
}
.styledLink {
    text-decoration: none;
}
.dropdownContainer {
    display: flex;
    flex-direction: column;
    position: absolute;
    align-items: center;
    z-index: 5;
    background: white;
    top: 60px;
    right: 35px;
}

.arrowContainer {
    position: relative;
}

.dropdown {
    border-radius: 2px;
    box-shadow: 0px 1px 4px 0px rgba(0, 0, 0, 0.26);
    z-index: 4;
    width: 292px;
    padding: 25px;
    display: flex;
    flex-wrap: wrap;
    border: 1px solid #ccc;
}

.square {
    width: 80px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 1px solid white;
}

.square:hover {
    border: 1px solid #e5e5e5;
    border-radius: 2px;
}

.iconStyle {
    font-size: 50px;
}

.initial {
    background: rgb(189, 55, 26);
    border-radius: 50%;
    color: white;
}

.small {
    width: 32px;
    height: 32px;
}

.large {
    width: 50px;
    height: 50px;
}

.initialContainer {
    padding-bottom: 10px;
}
.topLink {
    text-decoration: none;
    color: black;
}

.topLink:hover {
    text-decoration: underline;
}

.name {
  font-family: 'Roboto', sans-serif;
}


button {
    height: 36px;
    line-height: 27px;
    background-image: -webkit-gradient(
        linear,
        left top,
        left bottom,
        from(#f5f5f5),
        to(#f1f1f1)
    );
    background-image: -webkit-linear-gradient(top, #f5f5f5, #f1f1f1);
    -webkit-border-radius: 2px;
    -webkit-user-select: none;
    background-color: #f2f2f2;
    border: 1px solid #f2f2f2;
    border-radius: 2px;
    color: #757575;
    cursor: pointer;
    font-family: arial, sans-serif;
    /* font-size: 13px; */
    font-weight: bold;
    margin: 11px 4px;
    min-width: 54px;
    padding: 0 16px;
    text-align: center;
}

button:hover {
    background-image: -webkit-linear-gradient(top, #f8f8f8, #f1f1f1);
    background-color: #f8f8f8;
    border: 1px solid #c6c6c6;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
    color: #222;
}

.containerWrapper {
    box-shadow: 1px 1px 2px 1px rgba(0, 0, 0, 0.26);
    outline: 0;
    border-radius: 2px;
    max-width: 484px;
    width: 90%;
}

.flatBottomBr {
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
}
.grayBg {
    background: #eee;
}

.searchContainer {
    height: 40px;
    border-bottom: gray;
    line-height: 27px;
}

input {
    margin: 10px;
    outline: 0;
    border: white solid 0px;
    width: 93%;
}

.result {
    padding: 3px 10px;
}

.resultsContainer {
    border-top: 1px solid lightgray;
    box-shadow: 1px 1px 2px 1px rgba(0, 0, 0, 0.26);
    outline: 0;
    border-radius: 2px;
    max-width: 484px;
    width: 100%;
    background: white;
    z-index: 3;
}

.flatTopBr {
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
}

.card {
    border-radius: 4px;
    text-decoration: none;
    box-shadow: 1px 1px 2px 1px rgba(0, 0, 0, 0.26);
    width: 395px;
}

@media (max-width: 500px) {
    .card {
        width: 95%;
    }
}
.projectCard {
    box-shadow: 1px 2px 3px 2px rgba(0, 0, 0, 0.26);
    width: 100px;
    text-decoration: none;
    color: black;
    width: 350px;
}

.cardImg {
    box-shadow: 1px 2px 3px 2px rgba(0, 0, 0, 0.26);
}


.profilePic {
    width: 9rem;
    height: 100%;
    float: left;
    margin: 0 20px 10px 0;
    border-radius: 2px;
    /* border-radius: 50%; */
    /* shape-outside: circle(50%); */
    /* margin: 10 auto; */
}

.profilePic img {
    border-radius: 2px;
}

.contactItem {
    font-weight: 500;
    padding-left: 10px;
}

.linkWrapper {
    text-decoration: none;
    color: #5f6368;
    border-radius: 20px;
    padding: 12px;
}

.linkWrapper:hover {
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
    background: rgba(0, 0, 0, 0.2);
}

